<template>
  <breadcrumb-list :items="breadcrumbs"></breadcrumb-list>

  <div class="p-4 card xl:p-5">
    <form
      @input="request.errors.clear($event.target.name)"
      @submit.prevent="submit()"
    >
      <div class="space-y-4">
        <div>
          <label for="textName">{{ $t("attributes.name") }}</label>
          <span class="relative flex mt-1.5">
            <input
              v-model="role.name"
              id="textName"
              name="name"
              type="text"
              class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 pl-9 placeholder:text-slate-400/70 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
              autocomplete="off"
            />
            <span
              class="pointer-events-none absolute flex h-full w-10 items-center justify-center text-slate-400 peer-focus:text-primary dark:peer-focus:text-accent dark:text-navy-300"
            >
              <v-icon name="finger-print" size="6"></v-icon>
            </span>
          </span>

          <validation-feedback>
            {{ request.errors.get("name") }}
          </validation-feedback>
        </div>

        <div>
          <label class="inline-flex items-center space-x-2">
            <input
              v-model="role.is_admin"
              class="h-4 w-4 rounded border-slate-400/70 form-checkbox is-basic checked:bg-primary checked:border-primary hover:border-primary focus:border-primary dark:border-navy-400 dark:checked:bg-accent dark:checked:border-accent dark:hover:border-accent dark:focus:border-accent"
              type="checkbox"
            />
            <span>{{ $t("labels.is_admin") }}</span>
          </label>

          <validation-feedback>
            {{ request.errors.get("is_admin") }}
          </validation-feedback>
        </div>

        <div
          class="rounded-lg border border-slate-200 p-4 space-y-2 dark:border-navy-600"
        >
          <div v-for="ability in abilities" :key="ability.id">
            <span>
              <label class="inline-flex items-center space-x-2">
                <input
                  v-model="role.abilities"
                  class="h-4 w-4 rounded border-slate-400/70 form-checkbox is-basic checked:bg-primary checked:border-primary hover:border-primary focus:border-primary dark:border-navy-400 dark:checked:bg-accent dark:checked:border-accent dark:hover:border-accent dark:focus:border-accent"
                  type="checkbox"
                  :value="ability.id"
                />
                <span>{{ $t(`roles.abilities.${ability.name}`) }}</span>
              </label>
            </span>
          </div>
        </div>

        <validation-feedback>
          {{ request.errors.get("abilities") }}
        </validation-feedback>

        <div class="text-end space-x-3">
          <v-button
            type="submit"
            color="primary"
            :disabled="!request.submittable"
          >
            {{ $t("labels.submit") }}
          </v-button>
        </div>
      </div>
    </form>
  </div>

  <div class="mt-4 p-4 card">
    {{ JSON.stringify(this.role) }}
  </div>
</template>

<script>
import AbilityCollection from "@/collections/AbilityCollection";
import Role from "@/models/Role";
import ModelRequest from "@/request/ModelRequest";

export default {
  name: "RoleCreate",
  data() {
    return {
      breadcrumbs: [
        { title: this.$t("titles.roles"), route: "admin.roles" },
        {
          title: this.$t("models.role.add"),
          active: true,
        },
      ],
      abilities: new AbilityCollection(),
      role: new Role(),
      request: new ModelRequest(),
    };
  },
  created() {
    this.abilities.fetchAll();

    this.request.bind(this.role);
  },
  methods: {
    submit() {
      this.request.store({
        redirect: { name: "admin.roles" },
      });
    },
  },
};
</script>
