import api from "@/core/Api";
import i18n from "@/lang/i18n";
import Toast from "@/core/Toast";
import ErrorResponse from "@/request/ErrorResponse";
import router from "@/router";
import { REDIRECT_TIME } from "@/core/Settings";
import Model from "@/models/Model";
import { RouteLocationNamedRaw, RouteLocationRaw } from "vue-router";
import { serialize } from "object-to-formdata";
import { isArray } from "lodash";

const { t } = i18n.global;

type RequestType = "post" | "delete";

type Actions = "store" | "update" | "delete";

type Options = {
  action?: Actions;
  showSuccessToast?: true;
  showFailToast?: true;
  redirect?: RouteLocationRaw;
  idForRedirect?: boolean;
};

class ModelRequest {
  public errors: ErrorResponse = new ErrorResponse();
  public model: Model | null = null;
  public submittable: boolean = true;
  private defaultOptions: Options = {
    showSuccessToast: true,
    showFailToast: true,
  };

  constructor(model: Model | null = null) {
    this.model = model;
  }

  bind(model: Model): void {
    this.model = model;
  }

  getData(): Record<string, any> | FormData {
    const data: Record<string, any> = <Record<string, any>>(
      this.model?.getData()
    );

    if (this.hasFile(this.model)) {
      return serialize(data, {
        indices: true,
        booleansAsIntegers: true,
      });
    }

    return data;
  }

  hasFile(obj: any): boolean {
    for (const key in obj) {
      if (obj[key] instanceof File || obj[key] instanceof Blob) {
        return true;
      } else if (
        (typeof obj[key] === "object" && obj[key] !== null) ||
        isArray(obj[key])
      ) {
        if (this.hasFile(obj[key])) {
          return true;
        }
      }
    }
    return false;
  }

  submit(type: RequestType, url: string, options: Options): Promise<any> {
    this.beforeSubmit();

    const model: Model = <Model>this.model;

    const data: Record<string, any> = this.getData();

    return new Promise((resolve, reject) => {
      api[type](url, data)
        .then((response) => {
          if (options.showSuccessToast) {
            Toast.fire(t(`response.success.${options.action}`), "success");
          }

          if (
            typeof options.action !== "undefined" &&
            ["store", "update"].includes(options.action)
          ) {
            model.saveAttributes(response.data);
          }

          if (typeof options.redirect !== "undefined") {
            const redirect: RouteLocationNamedRaw = <RouteLocationNamedRaw>(
              options.redirect
            );

            if (options.idForRedirect && this.model) {
              redirect.params = {
                id: this.model.id,
              };
            }

            setTimeout(() => {
              router.push(redirect);
            }, REDIRECT_TIME);
          }

          return resolve(response);
        })
        .catch((error) => {
          this.errors.record(error);

          if (options.showFailToast) {
            Toast.fire(this.errors.getMessage(), "error");
          }

          return reject(error);
        })
        .finally(() => {
          this.afterSubmit();
        });
    });
  }

  beforeSubmit(): void {
    this.submittable = false;
  }

  afterSubmit(): void {
    this.submittable = true;
  }

  store(options: Options = {}): Promise<any> {
    const model: Model = <Model>this.model;

    options = {
      ...this.defaultOptions,
      action: "store",
      ...options,
    };

    return this.submit("post", `${model.getMainRoute()}/store`, options);
  }

  update(options: Options = {}): Promise<any> {
    const model: Model = <Model>this.model;

    options = {
      ...this.defaultOptions,
      action: "update",
      ...options,
    };

    return this.submit(
      "post",
      `${model.getMainRoute()}/update/${model.id}`,
      options
    );
  }

  delete(options: Options = {}): Promise<any> {
    const model: Model = <Model>this.model;

    options = {
      ...this.defaultOptions,
      action: "delete",
      ...options,
    };

    return this.submit(
      "delete",
      `${model.getMainRoute()}/delete/${model.id}`,
      options
    );
  }
}

export default ModelRequest;
